import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import {
	mapMutations,
	mapState
} from 'vuex';
export default {
	computed: {
		...mapState(['userInfo'])
	},
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'noMore',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'noMore',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'noMore',
					orderList: []
				},
				{
					state: 3,
					text: '历史订单',
					loadingType: 'noMore',
					orderList: []
				},
				{
					state: 4,
					text: '售后',
					loadingType: 'noMore',
					orderList: []
				}
			],
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = +options.state;




		if (options.state == 0) {
			this.loadData()
		}


	},

	methods: {
		// 显示订单详情
		showOrderDetail(orderNum) {
			//去往详情页
		},
		getCurOrder() {
			uni.request({
				url: this.constVal.baseURL + '/system/order/client_cur_order/' + this.userInfo.clientId,
				success: (res) => {
					// console.log(res.data);
					if (res.data.code == 200) {
						this.navList[2].orderList = res.data.rows;
					} else {
						uni.showToast({
							title: '获取待收货信息失败',
							image: '../../static/icons/close.png'
						})
					}
				}
			})
		},
		getHisOrder() {
			uni.request({
				url: this.constVal.baseURL + '/system/order/client_his_order/' + this.userInfo.clientId,
				success: (res) => {
					// console.log(res.data);
					if (res.data.code == 200) {
						this.navList[3].orderList = res.data.rows;
					} else {
						uni.showToast({
							title: '获取历史订单失败',
							image: '../../static/icons/close.png',
						})
					}
				}
			})
		},
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;

			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			navItem.loadingType = 'loading';

			setTimeout(() => {
				let orderList = Json.orderList.filter(item => {
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.state));
					//演示数据所以自己进行状态筛选
					if (state === 0) {
						//0为全部订单
						return item;
					}
					return item.state === state
				});
				orderList.forEach(item => {
					navItem.orderList.push(item);
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem, 'loaded', true);

				//判断是否还有数据， 有改为 more， 没有改为noMore 
				navItem.loadingType = 'noMore';
			}, 600);
			this.getCurOrder();
			this.getHisOrder();
		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		// 去往支付页面
		toPay() {

		},
		// 确认收货
		confirmDelivery(orderNum) {
			uni.showModal({
				content: '是否确认收货',
				success: (res) => {
					if (res.confirm) {
						setTimeout(() => {
							uni.request({
								url: this.constVal.baseURL + '/system/order/confirmToHome',
								data: {
									orderNum: orderNum,
								},
								method: 'POST',
								success: (res) => {
									console.log(res.data);
									this.loadData();
								}
							})
						}, 200)
					}
				}
			});
		},
		// 订单疑问
		questionOrder() {

		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip,
					stateTipColor
				})

				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val => val.id === item.id);
				index !== -1 && list.splice(index, 1);

				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待发货';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
